.table_container {
  background: #141b23;
  border-radius: 5px;
  color: #fff;
  margin-top: 50px;
  padding: 35px;
  .table_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    h4{
      font-size: 1.5rem !important;
      font-weight: 500;
      line-height: 1.2;
    }
    .serch_input {
      display: flex;
      flex: 1;
      background: #fff;
      border: 0;
      border-radius: 5px;
      color: #000;
      margin-top: 0;
      padding: 5px;
      min-height: 52px;
    }
  }
  table {
    thead {
      tr {
        height: 51px !important;
        th {
          width: 153px !important;
        }
      }
    }
    tbody {
      tr {
        height: 47px !important;
      }
    }
  }
}