.component_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #141b23;
  border-radius: 5px;
  box-shadow: none;
  margin: 20px;
  padding: 25px;
  width: 405px;
  .text_input {
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background:#202a35;
    border: 0;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    margin-bottom: 1rem;
  }
  .description_area{
    width: 100%;
    margin-bottom: 1rem;
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  }
  .btn_primary {
    background-color: #1153c8!important;
    border: none;
    border: 0!important;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    padding: 7px;
    width: 100%;
  }
}